import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { MessagingRoutingModule } from './messaging-routing.module';
import { CloseChatDialogComponent, MessagingComponent, NotifDialogComponent } from './messaging.component';
import { AqInputComponent } from './aq-input/aq-input.component';
import { MessageFragmentComponent } from './message-fragment/message-fragment.component';
import {MatCardModule} from '@angular/material/card';
import {MatButtonModule} from '@angular/material/button';
import {MatIconModule} from '@angular/material/icon';
import { HttpClientModule } from '@angular/common/http';
import {TextFieldModule} from '@angular/cdk/text-field';
import {MatToolbarModule} from '@angular/material/toolbar';
import {MatDialogModule} from '@angular/material/dialog';
import {MatListModule} from '@angular/material/list';
import {MatSlideToggleModule} from '@angular/material/slide-toggle';

@NgModule({
  declarations: [MessagingComponent, AqInputComponent, MessageFragmentComponent, NotifDialogComponent, CloseChatDialogComponent],
  imports: [
    CommonModule,
    MessagingRoutingModule,
    MatCardModule,
    MatButtonModule,
    MatIconModule,
    HttpClientModule,
    TextFieldModule,
    MatToolbarModule,
    MatDialogModule,
    MatListModule,
    MatSlideToggleModule
  ]
})
export class MessagingModule { }
