import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Stomp } from '@stomp/stompjs';
import { Observable, Subject } from 'rxjs';
import * as SockJS from 'sockjs-client';
import { Globals } from '../global.constants';
import { UserService } from '../user.service';

@Injectable({
  providedIn: 'any'
})
export class MessagingService {

  private connection: any;
  messages = new Subject<any>();
  events = new Subject<any>();


  constructor(private http: HttpClient, private userService: UserService) {
    this.connection = Stomp.over(new SockJS(Globals.ENDPOINT + Globals.SOCKET_ENDPOINT));
    this.connection.connect({}, () => {
      this.connection.subscribe(Globals.MESSAGE_SOCKET + userService.getTopicName(), (response: any) => {
        const message = JSON.parse(String(new TextDecoder('utf-8').decode(response._binaryBody)));
        if (message.type === Globals._TYPE_EVENT) {
          this.events.next(message);
        }
        else if (message.type === Globals._TYPE_MESSAGE) {
          this.messages.next(message);
        }
      });
    });
  }

  /* @comment
  * send message or events to the server
  */
  public sendMessage(message: any, topic: string): void {
    this.http.post(Globals.ENDPOINT + Globals.SEND_API_ENDPOINT, message,
    {headers: {topic, 'Content-Type': 'application/json; charset=utf-8'}})
    .subscribe();
  }


}
