import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Globals } from '../global.constants';
import { UserService } from '../user.service';
import { AqInputComponent } from './aq-input/aq-input.component';
import { MessagingService } from './messaging-service.service';

@Component({
  selector: 'app-messaging',
  templateUrl: './messaging.component.html',
  styleUrls: ['./messaging.component.css']
})
export class MessagingComponent implements OnInit {

  constructor(public dialog: MatDialog,
              private messageService: MessagingService,
              private userService: UserService,
              private router: Router) { }

  ngOnInit(): void {
  }

  openCloseDialog(): void {
    const dialogRef = this.dialog.open(CloseChatDialogComponent);

    dialogRef.afterClosed().subscribe(result => {
    });
  }

  openNotificationDialog(): void {
    const dialogRef = this.dialog.open(NotifDialogComponent);
    dialogRef.afterClosed().subscribe(result => {
    });
  }
}

@Component({
  selector: 'app-close-chat-dialog',
  templateUrl: 'close-chat-dialog.html',
})
export class CloseChatDialogComponent {

  constructor(private http: HttpClient,
              private messageService: MessagingService,
              private userService: UserService,
              private router: Router) {

  }

  closeChat(): void {
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json');
    this.http.get(Globals.ENDPOINT + Globals.ROOM_CLOSE_ENDPOINT + this.userService.getTopicName(), {headers, responseType: 'json'})
      .subscribe((response: any) => {
        this.messageService.sendMessage({
          username: this.userService.getUserName(),
          message: 'CLOSED_ROOM',
          type: Globals._TYPE_EVENT,
          timestamp: ''
        }, this.userService.getTopicName());
      });

    this.router.navigate(['feedback']);
  }

}

@Component({
  selector: 'app-notif-dialog',
  templateUrl: 'notif-dialog.html',
})
export class NotifDialogComponent {}
