import { AfterViewChecked, Component  , OnInit, ViewChild } from '@angular/core';
import { UserService } from 'src/app/user.service';
import { MessagingService } from '../messaging-service.service';

@Component({
  selector: 'app-message-fragment',
  templateUrl: './message-fragment.component.html',
  styleUrls: ['./message-fragment.component.css']
})
export class MessageFragmentComponent implements OnInit, AfterViewChecked {

  @ViewChild('mcontainer') messageContainer: any;

  messages: any[] = [
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u1-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u2-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u1-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u2-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u2-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u1-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u2-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u1-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u1-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u2-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u2-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u1-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u2-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u2-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u1-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u2-message'},
    // {username: 'kart', message: 'Hi', type: '', timestamp: '', class: 'u1-message'},
  ];

  constructor(public messagingService: MessagingService, private userService: UserService) { }

  ngAfterViewChecked(): void {
    this.scrollToBottom();
  }

  scrollToBottom(): void {
    this.messageContainer.nativeElement.scrollTop = this.messageContainer.nativeElement.scrollHeight;
  }

  ngOnInit(): void {
    this.messagingService.messages
    .subscribe(value => {
      if (this.userService.getUserName() === value.username) {
          value.class = 'u1-message';
        }
        else {
          value.class = 'u2-message';
        }
      this.messages.push(value);
      this.scrollToBottom();
      this.playSound();
    }, error => {
      // alert an error here
      console.error(error);
    });
  }

  playSound(): void {
    const audio = new Audio();
    audio.src = '../../../assets/blop.mp3';
    audio.load();
    audio.play();
  }

}
