import { Component, ElementRef, EventEmitter, OnInit, ViewChild } from '@angular/core';
import { UserService } from 'src/app/user.service';
import { MessagingService } from '../messaging-service.service';
import { Router } from '@angular/router';
import { Globals } from 'src/app/global.constants';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';


@Component({
  selector: 'app-aq-input',
  templateUrl: './aq-input.component.html',
  styleUrls: ['./aq-input.component.css']
})
export class AqInputComponent implements OnInit {

  EVENT_TYPE_STARTED_TYPING = 'STARTED_TYPING';
  EVENT_TYPE_STOPPED_TYPING = 'STOPPED_TYPING';
  EVENT_TYPE_CLOSED_ROOM = 'CLOSED_ROOM';
  EVENT_TYPE_JOINED_ROOM = 'JOINED_ROOM';

  @ViewChild('textMain') inputBox!: ElementRef;
  @ViewChild('typingNotif') typingNotif!: ElementRef;
  sessionMessage = 'Oh! The other user has not joined yet. You can leave this room and you will be notified with a blop sound when they join.' ;

  typingEvent: EventEmitter<any> = new EventEmitter<any>();

  sessionEnded = true;

  constructor(private router: Router, private messagingService: MessagingService, private userService: UserService) { }

  ngOnInit(): void {
    if (this.userService.getUserName() === undefined || this.userService.getUserName() === null) {
      // this.router.navigate(['']);
    }
    this.sendTypingEvent(this.EVENT_TYPE_JOINED_ROOM);
    this.detectTyping();
    this.subscribeToMessageEvents();
  }

  detectTyping(): void {
    this.typingEvent.subscribe((event: any) => {
      setTimeout(() => {
          this.sendTypingEvent(this.EVENT_TYPE_STOPPED_TYPING);
      }, 3000);
      if (event.key === 'Enter') {
        this.sendMessage();
        return;
      }
      this.sendTypingEvent(this.EVENT_TYPE_STARTED_TYPING);
    },
    null,
    null);
  }

  sendMessage(): void {
    if (this.inputBox.nativeElement.value === '') {
      return;
    }
    this.messagingService.sendMessage({
      username: this.userService.getUserName(),
      message: this.inputBox.nativeElement.value,
      type: Globals._TYPE_MESSAGE,
      timestamp: ''
    }, this.userService.getTopicName());
    this.inputBox.nativeElement.value = '';
    this.sendTypingEvent(this.EVENT_TYPE_STOPPED_TYPING);
  }

  sendTypingEvent(typingEvent: string): void {
    this.messagingService.sendMessage({
      username: this.userService.getUserName(),
      message: typingEvent,
      type: Globals._TYPE_EVENT,
      timestamp: ''
    }, this.userService.getTopicName());
  }

  subscribeToMessageEvents(): void {
    this.messagingService.events.subscribe((response: any) => {

      if (response.username === this.userService.getUserName()) {
        return;
      }
      if (response.message === this.EVENT_TYPE_STARTED_TYPING && this.typingNotif !== undefined) {
        this.typingNotif.nativeElement.innerHTML = response.username + ' is typing...';
        if (this.sessionEnded === true) {
          this.sessionEnded = false;
          this.playSound();
        }
      }
      else if (response.message === this.EVENT_TYPE_JOINED_ROOM && this.sessionEnded === true) {
        this.sessionEnded = false;
        setTimeout(() => {
          this.sendTypingEvent(this.EVENT_TYPE_JOINED_ROOM);
        }, 5000);
        this.playSound();
      }
      else if (response.message === this.EVENT_TYPE_STOPPED_TYPING) {
        this.typingNotif.nativeElement.innerHTML = '';
      }
      else if (response.message === this.EVENT_TYPE_CLOSED_ROOM) {
        this.sessionMessage = 'The session has been ended by the other user.';
        this.sessionEnded = true;
      }
    });
  }

  playSound(): void {
    const audio = new Audio();
    audio.src = '../../../assets/blop.mp3';
    audio.load();
    audio.play();
  }
}
